export default class Mapx extends Map {
    constructor(obj) {
        super();
        this.index = 0;
        if (Array.isArray(obj)) {
            if (Array.isArray(obj[0])) {
                this.setArray2D(obj);
            }
            else {
                this.setArray1D(obj);
            }
        }
        else if (typeof obj === "object") {
            this.setObject(obj);
        }
    }
    get length() {
        return this.size;
    }
    set length(n) {
        let i = 0;
        this.forEach((v, k) => {
            if (i++ >= n) {
                this.delete(k);
            }
        });
    }
    setArray1D(arr) {
        let keys = [];
        for (let o = 0; o < arr.length; o++) {
            let key = this.getIndex();
            this.set(key, arr[o]);
            keys.push(key);
        }
        return keys;
    }
    setArray2D(arr) {
        let key, keys = [];
        for (let o = 0; o < arr.length; o++) {
            if (Array.isArray(arr[o])) {
                keys.push(arr[o][0]);
                this.set(arr[o][0], arr[o][1]);
            }
            else {
                key = this.getIndex();
                keys.push(key);
                this.set(key, arr[o]);
            }
        }
        return keys;
    }
    setObject(obj) {
        let keys = [];
        for (let o in obj) {
            keys.push(o);
            this.set(o, obj[o]);
        }
        return keys;
    }
    toArray() {
        return [...this];
    }
    toObject() {
        let obj = {};
        for (let [k, v] of this.entries()) {
            if (typeof k === "string" || typeof k === "number") {
                obj[k] = v;
            }
        }
        return obj;
    }
    mapToArray(c) {
        return this.toArray().map((v, k, a) => {
            return c(v[1], v[0], a);
        });
    }
    clone() {
        return new Mapx(this.toArray());
    }
    map(c) {
        let m = this.clone();
        for (let [k, v] of this.entries()) {
            m.set(k, c(v, k, m));
        }
        return m;
    }
    filterToArray(c) {
        return this.toArray().filter((v, k, a) => {
            return c(v[1], v[0], a);
        });
    }
    filter(c) {
        let m = this.clone();
        for (let [k, v] of this.entries()) {
            if (!c(v, k, m)) {
                m.delete(k);
            }
        }
        return m;
    }
    reduce(c, r) {
        return this.toArray().reduce((r, v, k, a) => {
            return c(r, v[1], v[0], a);
        }, r);
    }
    getIndex() {
        return '__' + this.index.toString(32);
    }
    updateIndex() {
        this.index++;
    }
    set(key, value) {
        super.set(key, value);
        this.updateIndex();
        return this;
    }
    push(value) {
        let key = this.getIndex();
        this.set(key, value);
        return key;
    }
    pop() {
        let i = 0;
        if (this.size) {
            for (let kv of this.entries()) {
                if (++i >= this.size) {
                    this.delete(kv[0]);
                    return kv[1];
                }
            }
        }
        else {
            return;
        }
    }
    toValues() {
        return [...this.values()];
    }
    toKeys() {
        return [...this.keys()];
    }
    indexOf(value) {
        for (let kv of this.entries()) {
            if (kv[1] === value) {
                return kv[0];
            }
        }
        return null;
    }
    shift() {
        let v = this.entries().next();
        this.delete(v[0]);
        return v[1];
    }
}
